/* Global styling start */
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #370cc0;
  background-color: #1b1b1b;
  font-family: "Lato", sans-serif;
  height: 100%;
  width: 100%;
  background-size: 200% 200%;
  padding-top: 100px;
}

@keyframes animate-background {
  0% {
    background-position: 10% 0%;
  }
  50% {
    background-position: 91% 100%;
  }
  100% {
    background-position: 10% 0%;
  }
}

a {
  text-decoration: none;
}

/* Global styling end
  ******************************************
*/

/* user card styling start */

.user-card {
  /* background-color: rgba(255, 255, 255, 0.3); */
  background: linear-gradient(
    to right,
    hsla(180, 100%, 51%, 0.2),
    hsla(299, 100%, 50%, 0.2)
  );
  margin-top: 5rem;
  background-size: 200% 200%;
  animation: animate-background 10s infinite;
}

.user-card .profile-image-wrapper {
  width: 120px;
  background: #fc00ff; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    black,
    #fc00ff
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #00dbde,
    #fc00ff
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-size: 200% 200%;
  animation: animate-background 5s infinite;
  z-index: 1;
}

.user-card .profile-image-wrapper img {
  height: 100px;
  width: 120px;
  object-fit: cover;
  z-index: -1;
}

/* user card styling end
   *******************************
*/

/* Post excerpt card styling end */

.post-excerpt img {
  max-height: 200px;
  width: auto;
  filter: opacity(90%) brightness(90%);
}

/* Post excerpt card styling end 
   *******************************
*/

.posts-by .post-by-wrapper {
  max-width: 300px;
}

/*---------------------*/

/* comment */

.comment-image-wrapper{
  height: 40px;
  width: 40px;
  transform: translate(calc(-100% - 10px), 5px);
}

.comment-image-wrapper .comment-image {
  object-fit: cover;
}

/* comment styling end*/

